:root {
  --light-1: #ffffff;
  --light-2: #b1b1b1;
  --green-1: #00ffa2;
  --green-2: #00ffa2;
  --dark-1: #131516;
  --dark-2: #212426;
  --dark-3: #191b1d;
}

.App {
  text-align: center;
}

main {
  height: 100vh;
  position: relative;
  width: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: OpenSans-Bold;
  src: url("../src/fonts/Open_Sans/static/OpenSans/OpenSans-SemiBoldItalic.ttf");
}

@font-face {
  font-family: OpenSans-Italic;
  src: url("../src/fonts/Open_Sans/static/OpenSans/OpenSans-MediumItalic.ttf");
}

@font-face {
  font-family: OpenSans-LightItalic;
  src: url("../src/fonts/Open_Sans/static/OpenSans/OpenSans-LightItalic.ttf");
}

@font-face {
  font-family: OpenSans-Light;
  src: url("../src/fonts/Open_Sans/static/OpenSans/OpenSans-Light.ttf");
}

h1 {
  color: var(--green-1);
  font-family: OpenSans-Bold;
}

p {
  color: var(--light-1);
}

h2,
h3,
h4,
h5,
h6,
button {
  color: var(--light-1);
  font-family: OpenSans-LightItalic;
}

span.fine-print {
  font-family: OpenSans-LightItalic;
  color: var(--light-2);
  font-size: 10px;
}

select.form-select {
  background-color: var(--dark-1);
  color: var(--green-1);
  width: 250px;
}

div {
  color: var(--green-1);
  font-family: OpenSans-Light;
}

div.header {
  display: flex;
  justify-content: space-between;
  background-color: var(--dark-1);
  padding: 10px 30px;
}

input {
  color: var(--light-1);
  border: none;
  font-family: OpenSans-LightItalic;
  font-size: 10px;
  background-color: var(--dark-2);
}

input.inputField {
  color: var(--light-1);
  border: none;
  font-family: OpenSans-LightItalic;
  font-size: 30px;
  background-color: var(--dark-1);
}

i {
  font-size: 2em;
  color: var(--green-1);
}

a {
  text-decoration: none !important;
}

#root {
  background-color: white;
}

div.card {
  background-color: var(--dark-1);
  border-color: transparent;
  border-radius: 15px;
  padding: 20px;
  /* box-shadow: 0px 0px 5px rgba(192, 192, 192, 0.342); */
}

/* div.card:hover {
  box-shadow: 0px 0px 20px rgba(192, 192, 192, 0.342);
} */

main {
  background-color: var(--dark-2);
}

button.controls {
  background-color: var(--dark-1);
  border: none;
  border-radius: 10px;
  padding: 0px 10px;
  color: var(--green-1);
  text-align: center;
  text-decoration: none;
  display: inline-block;
  height: 40px;
  font-family: OpenSans-LightItalic;
  font-size: 20px;
  margin: 0px;
}

button.controls2 {
  background-color: var(--dark-2);
  border: none;
  border-radius: 10px;
  padding: 0px 10px;
  color: var(--light-1);
  text-align: center;
  text-decoration: none;
  display: inline-block;
  height: 40px;
  font-family: OpenSans-Light;
  font-size: 20px;
  margin: 0px;
}

button.delete {
  background-color: var(--dark-2);
  border: none;
  border-radius: 20px;
  padding: 0px 10px;
  color: var(--light-1);
  text-align: center;
  text-decoration: none;
  display: inline-block;
  height: 30px;
  font-family: OpenSans-Light;
  font-size: 20px;
  margin: 0px;
}
